import React, {Component} from 'react';
import Cell from './Rest/Cell';


export default class Map extends Component{
    constructor(props){
        super(props);

        let clientWidth = document.documentElement.clientWidth;
        let clientHeight = document.documentElement.clientHeight;

        this.state = {
            clientWidth,
            clientHeight,
            countColumn: Math.floor(clientHeight / 50),
            countRows: Math.floor(clientWidth / 50),
            grid : [],

        };
    }

    drawCells(){
        for(let i = 0; i < 10;i++){
            return (<Cell/>);
        }
    }

    render(){


        for(let i = 0;i < this.state.countRows; i++){

            this.state.grid[i] = [];
            for(let j = 0;j < this.state.countColumn; j++){
                this.state.grid[i][j] = 0;
            }
        }
        return(
            <div className="map">
                {`${this.state.countRows} : ${this.state.countColumn}`}
                {this.drawCells()}
            </div>
        );
    }
}